.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

label {
  margin: 5px;
  display: block;
}

label input {
  margin: 10px;
}

.tip {
  color: red;
}

.tin {
  color: black;
}

.TaskItem {
  display: block;
  background-color: #f5f5f5;
  padding: 10px;
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 10px;
}

.option {
  padding: 10px;
  border: none;
  background-color: #bbb;
}

.option:hover {
  padding: 10px;
  border: none;
  background-color: #999;
}
